:root {

    --off_White: rgb(245, 241, 237);

    --gray: rgba(0, 0, 0, 0.582);
}

.MissionCard {
    background-color: var(--gray);
    border: solid var(--off_White);
    border-radius: 12px;
    border-width: 1px;
    display: flex;
    flex-direction: column;
    font-size: 1.8rem;
    height: 300px;
    margin: 30px;
    text-align: left;
    width: 455px;
}

.missionName {
    font-size: 2.3rem;
    font-weight: 1000;
    padding: 5%;
}

.missionYcdest {
    border-top: solid var(--off_White);
    border-width: 1px;
    line-height: 35px;
    padding: 5%;
}
